body {
  font-family: 'Roboto', Arial, sans-serif;
  line-height: 1.6;
  color: #333;
  background-color: #f4f4f4;
  margin: 0;
  padding: 0;
}

.json-comparison {
  padding-top: 2rem;
  padding-bottom: 2rem;
}

.input-section {
  padding: 2rem;
  margin-bottom: 2rem;
}

.compare-icon {
  display: flex;
  justify-content: center;
  align-items: center;
}

.results-section {
  padding: 2rem;
}

.page-section {
  margin-bottom: 1rem !important;
  border: 1px solid #e0e0e0;
  border-radius: 4px;
  overflow: hidden;
}

.page-url {
  background-color: #f5f5f5;
  padding: 0.75rem;
  margin: 0;
  cursor: pointer;
  user-select: none;
  font-size: 0.9rem !important;
  color: #2c3e50 !important;
  word-break: break-all;
  display: flex;
  align-items: center;
}

.page-content {
  padding: 1rem;
}

.missing {
  color: #e74c3c;
  text-decoration: line-through;
}

.added {
  color: #2ecc71;
}

ul {
  list-style-type: none;
  padding-left: 0;
  margin: 0;
}

li {
  margin-bottom: 0.25rem;
  font-size: 0.8rem;
  word-break: break-all;
  display: flex;
  align-items: center;
}

.error-section {
  padding: 1rem;
  margin-bottom: 1rem;
}

.MuiAccordionSummary-root {
  background-color: #f5f5f5;
}

.MuiAccordionDetails-root {
  padding: 1rem !important;
}
