body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 20px;
}

.App {
  max-width: 100%;
  margin: 20px 20px;
}

input[type="file"] {
  margin-right: 10px;
}

button {
  margin-top: 10px;
}

pre {
  background-color: #f0f0f0;
  padding: 10px;
  border-radius: 5px;
  white-space: pre-wrap;
  word-wrap: break-word;
}
